.module-work{
	@include fullwidth-container(transparent);
	
}

.module-work-inner{
	// margin-left: -$page-margin;
	// margin-right: -$page-margin;
	// padding-left: $page-margin;
	// padding-right: $page-margin;
	padding-top: 20px;
	

	.module-work-title{
		font-weight: normal;
		font-size: 26px;
		margin-bottom: $paragraph-margin-bottom;
		a{
			color: $bregne;
		}
	}


	.module-work-items{
		display: flex;
		flex-wrap: nowrap;
		overflow-x: auto;
		margin-left: -$page-margin;
		margin-right: -$page-margin;
		padding-left: $page-margin;
		padding-right: $page-margin;
		padding-bottom: $grid-gutter-width;
		-webkit-overflow-scrolling: touch;
		// border: 1px solid red;
	}
		&::-webkit-scrollbar {
			display: none;
		}
		.module-work-item{
			// border: 1px solid pink;
			width: 225px;
			margin-right: $grid-gutter-width;
			flex: 0 0 225px;
			picture{
				display: block;
				margin-bottom: $paragraph-margin-bottom;
			}
		}

			.module-work-tags{
				padding: 0;
				// margin: 0;
				li{
					display: inline;
					a{
						font-family: FraktionSans;
						font-size: 16px;
						color: $body-color;
						text-decoration: none;
					}
				}
			}

			.module-work-item-title{
				font-size: 26px;
				
				a{
					color: $txt-color;
				}
			}

			.module-work-introTxt{
				// margin-bottom: 0;
			}



}
@media (min-width: $screen-md-min) {
	.module-work{
		margin: $grid-gutter-width 0 $grid-gutter-width 0;
		padding-left: 0;
		padding-right: 0;
		
	}
		.module-work-inner{
			// border: 1px dotted purple;
			margin: 0;
			padding: 0;
			
			.module-work-title{
				margin-bottom: 70px;
			}
		
			.module-work-items{
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				max-width: 100%;
				margin: 0;
				padding: 0;
				overflow-x: visible;
				// border: 1px dotted orangered;
			}
				.module-work-item{
					width: 30%;
					flex: 0 0 30%;
					margin: 0 0 $grid-gutter-width 0;
				}
					.module-work-item-title{
						
					}
		}
}


